* {
  box-sizing: border-box;
}

body{
  margin:0;
}

.site-title  {
  font-size: 2rem;
}

.nav {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch; /*hover spans entire container*/
  gap: 2rem; /*space in between elements*/
  padding: 0 1rem; /*left and right spaces*/
}

.nav ul{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display:flex;
  align-items: center;
  padding: .25rem;
}

.nav li.active{
  background-color: #555;
}

.nav li:hover{
  background-color: #777;
}

